import Products from "model/Products";
import CookieManager from "service/CookieManager";

let User = {

    dummyData : [
        "SILDAM/EUS/IVYGLD",
        "SILDAM/CHA/IVY",
        "SILDAM/LOM/GRNRED",
        "SILDAM/CSM/REDBLK",
        "SILDAM/PER/IVY"
    ],

    data : {
        userDetails : {
            name : "Laura",
            email : "laura@laura.laura",
            privilege : "normal"
        },

        swatches : []
    },

    test : function () {
        User.data.swatches = User.dummyData;
        // console.log("/User/ -test ", User.data.swatches );
    },
    
    init : function () {
        User.data.swatches = CookieManager.getData().swatches || [];
    },

    add : function ( _data ) {

        console.log("/User/ -add ", User.data );

        User.data.swatches.push( _data );
        User.writeToCookie();


        // console.log("/User/ -add ", _data, User.data.swatches );
    },

    remove : function ( _data ) {
        let tgt = [ _data ];
        User.data.swatches = _.pullAll( User.data.swatches, tgt );
        User.writeToCookie();

        console.log("/User/ -remove ", _data, User.data.swatches );
    },

    getMySwatches : function () {
        return Products.methods().getMySwatchesFromCodes( User.data.swatches );
    },

    checkHasBeenAddedFromCode : function ( _code ) {

        let z = _.find( User.data.swatches, function ( _v ) {
            return _v === _code
        } );

        // console.log("/User/ -checkHasBeenAddedFromCode ", _code, z, !!z );

        return !!z
    },


    writeToCookie : function () {

        let vo = {
            swatches : User.data.swatches
        };

        CookieManager.addSessionVar( vo );
    }

};

export default User


