import React from "react";
import ReactDOM from "react-dom";
import Globals from "Globals";
import TweenMax from "gsap";

// import TransitionMixin from "utils/TransitionMixin";


export default function GatewayView( api ) {

    let events = {

    };

    let els = {
        el : null,
        reactEl : null
    };

    let props = {
        data : null,
        transitionMixin : null
    };

    let uis = {
        component : null
    };

    let _react = {
        component : null
    };

    const init = function ( _config ) {

        els.el = _config.el;

        /*props.transitionMixin = _config.mixins[ 0 ];
         props.transitionMixin.init( { id : api.info.name, el : els.el } );*/

        create();
        ( _config.render ) ? render() : null;
    };

    const create = function () {

        _react.component = React.createClass({

            getInitialState : function () {
                return null
            },

            componentDidMount : function () {
                els.reactEl = ReactDOM.findDOMNode( this );

            },

            update : function () {

            },

            onLoginBtnClicked : function ( e ) {
                actionRunner( "on-login-btn-clicked", null );
            },

            render : function () {


                return (
                    <div>

                        <div className="background"></div>

                        <div className="form-container">

                            <div className="inner">

                                <div className="top-panel">
                                    <div className="logo"></div>
                                </div>

                                <div className="hbox">

                                    <div>
                                        <h2>REGISTER</h2>

                                        <input type="text" placeholder="your full name..."/>
                                        <input type="text" placeholder="your company..."/>
                                        <input type="text" placeholder="your email..."/>

                                        <p>We will send you an email shortly confirming your registration</p>

                                        <div className="btn"><span>SUBMIT</span></div>

                                    </div>


                                    <div>
                                        <h2>Already Registered?</h2>

                                        <input type="text" placeholder="your email..."/>

                                        <div className="btn" onClick={ this.onLoginBtnClicked }><span>LOGIN</span></div>
                                    </div>

                                </div>

                            </div>

                        </div>



                    </div>
                );

                //<p>TRADE ONLY</p>
            }

        })
    };

    const render = function () {

        uis.component = ReactDOM.render(
            React.createElement( _react.component ), els.el
        );

    };


    const validateLogin = function ( _data ) {

        console.log("/GatewayView/ -validateLogin SHOULD VALIDATE");

        let r = true;
        // TODO
        return r
    };



    const actionRunner = function( _action, _data ){

        // console.log("/GatewayView/ -actionRunner ", _action, _data );

        switch( _action ){

            case "show":
                // props.transitionMixin.showHide( true );
                break;

            case "dispose":
                console.log("/GatewayView/ -actionRunner DISPOSE");
                ReactDOM.unmountComponentAtNode( els.el );

                break;


            case "on-login-btn-clicked":

                // console.log("/GatewayView/ -actionRunner SHOULD VALIDATE", _action );

                if( validateLogin( _data ) ){

                    api.dispatchRequest({
                        action : "on-gateway-passed",
                        data : null
                    });

                    //TweenMax.to( els.el, 0.5, { top : -100 } );

                }


                break;

        }
    };

    return{
        els : els,
        init : init,
        actionRunner : actionRunner
    }
}


