import signals from "signals";
// import Cookies from "../../.libs/cookies.min.js" // *** non-npm libs here
import Cookies from "../../.libs/js.cookie.js" // *** non-npm libs here


let CookieManager = {

    events : {
        Sg_COOKIE_STATUS : new signals.Signal()
    },

    els : {

    },

    props : {
        cookieName : "hopkins_shop_cookie",
        trueValue : "yes",
        falseValue : "no",
        blankSymbol : "+",
        sessionVars_$ : [],
        hasCookie : false,

        cookieObject : null
    },

    init : function(){
        // //console.log("/CookieManager/ -init ");
        ////console.log("/CookieManager/ -init->", Cookies.get( CookieManager.props.cookieName ));
        CookieManager.checkCookie();
    },

    checkCookie : function(){

        CookieManager.props.cookieObject = Cookies.get( CookieManager.props.cookieName );
        CookieManager.props.hasCookie = !!CookieManager.props.cookieObject;

        //console.log("/CookieManager/ -checkCookie COOKIE?", CookieManager.props.cookieObject );

        if( !CookieManager.props.cookieObject ){
            //console.log("/CookieManager/ -checkCookie NO COOKIE - SHOULD CREATE BLANK");
            CookieManager.writeCookie();

        } else {

            //console.log("/CookieManager/ -checkCookie HAS COOKIE - DO NOTHING (WAIT FOR DATA TO BE REQUESTED)");

            /*if( CookieManager.props.cookieObject === CookieManager.props.blankSymbol ){
                //console.log("/CookieManager/ -serialiseData HAS COOKIE BUT IS BLANK - SHOULD IGNORE IT AND USE A DEFAULT VALUE");
            } else {
                //console.log("/CookieManager/ -checkCookie HAS COOKIE - SHOULD PARSE IT AND STORE DESERIALISED DATA", CookieManager.props.cookieObject );
                CookieManager.deserialiseData( CookieManager.props.cookieObject );
            }*/
        }
    },


    writeCookie : function( _data ){

        let d = _data || CookieManager.props.blankSymbol;
        Cookies.set( CookieManager.props.cookieName, d );

        //console.log("/CookieManager/ -writeCookie HAS WRITTEN", d );
    },


    addSessionVar : function( _value ){
        CookieManager.writeCookie( _value );
    },

    getData : function () {

        let r = [];

        if( CookieManager.props.cookieObject !== CookieManager.props.blankSymbol ){
            r = JSON.parse( CookieManager.props.cookieObject )
        }

        return r
    }
};

export default {
    events : CookieManager.events,
    props : CookieManager.props,
    init : CookieManager.init,
    getData : CookieManager.getData,
    flush : CookieManager.flush,
    writeCookie : CookieManager.writeCookie,
    deleteCookie : CookieManager.deleteCookie,
    addSessionVar : CookieManager.addSessionVar
}





